/*------ Social-box -----*/

.social-box {
  min-height: 160px;
  margin-bottom: 20px;
  text-align: center;
  background: $white;
  border-radius: 8px;
  box-shadow: 0 0 0 1px rgba(181, 201, 227, 0.12), 0 8px 16px 0 rgba(181, 201, 227, 0.24);

  i {
    display: block;
    font-size: 40px;
    background: #e9ecef;
    line-height: 110px;
  }

  &.facebook i {
    color: $white;
    background: #5e5baa;
    border-radius: 8px 8px 0 0;
  }

  ul {
    list-style: none;
    display: inline-block;
    margin: 7px 0 0;
    padding: 10px;
    width: 100%;

    li {
      color: #949ca0;
      font-size: 14px;
      font-weight: 700;
      padding: 0 10px 0 0;
      text-align: center;
      display: block;
      float: left;
      width: 50%;
      line-height: 2;

      strong {
        display: block;
        font-size: 17px;
        line-height: 1;
        color: $color;
      }

      span {
        font-size: 15px;
        font-weight: 500;
        color: $color;
        text-transform: uppercase;
      }

      &:last-child {
        padding-left: 10px;
        padding-right: 0;
        text-align: center;
      }
    }
  }

  &.twitter i {
    color: $white;
    background: #00bcd4;
    border-radius: 8px 8px 0 0;
  }

  &.linkedin i {
    color: $white;
    border-radius: 8px 8px 0 0;
  }

  &.google-plus i {
    color: $white;
    background: #ff3b2e;
    border-radius: 8px 8px 0 0;
  }
}

