    font-size: 13px;
    margin: 0 0 .2rem 0;
    color: #313148;
  }

  img {
    position: relative;
    display: block;
    width: 450px;
    border-radius: 5px;
    box-shadow: 0 0 3px #eee;
    transition: all 0.4s cubic-bezier(0.565, -0.26, 0.255, 1.41);
    cursor: default;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
  }

  time {
    font-size: .7rem;
    color: #a7b4c9;
    margin-top: 3px;
    float: right;
    cursor: default;
    -webkit-touch-callout: none;
