    border-bottom-left-radius: 0 !important;
  }

  &:last-child .page-link {
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
  }

  &.active .page-link {
    z-index: 1;
    color: $white;
  }

  &.disabled .page-link {
    color: #ced4da;
    pointer-events: none;
    cursor: auto;
    background-color: $white;
    border-color: #e8ebf3;
  }
