/*-----Multiple Widgets Styles-----*/

/*----Image Check----*/

.imagecheck {
  margin: 0;
  position: relative;
  cursor: pointer;
}

.imagecheck-input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.imagecheck-figure {
  border: 1px solid #e8ebf3;
  border-radius: 3px;
  margin: 0;
  position: relative;
  height: 100%;
}

.imagecheck-input:checked ~ .imagecheck-figure {
  border-color: rgba(0, 40, 100, 0.24);
}

.imagecheck-figure:before {
  content: '';
  position: absolute;
  top: .25rem;
  left: .25rem;
  display: block;
  width: 1rem;
  height: 1rem;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  z-index: 1;
  border-radius: 3px;
  opacity: 0;
  transition: .3s opacity;
}

.imagecheck-input:checked ~ .imagecheck-figure:before {
  opacity: 1;
}

.imagecheck-image {
  max-width: 100%;
  opacity: .64;
  transition: .3s opacity;

  &:first-child {
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
  }

  &:last-child {
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
  }
}

.imagecheck-input {
  &:checked ~ .imagecheck-figure .imagecheck-image, &:focus ~ .imagecheck-figure .imagecheck-image {
    opacity: 1;
  }
}

.imagecheck:hover .imagecheck-image {
  opacity: 1;
}

.imagecheck-caption {
  text-align: center;
  padding: .25rem .25rem;
  color: #a7b4c9;
  font-size: .875rem;
  transition: .3s color;
}

.imagecheck-input {
  &:checked ~ .imagecheck-figure .imagecheck-caption, &:focus ~ .imagecheck-figure .imagecheck-caption {
    color: #3d4e67;
  }
}

.imagecheck:hover .imagecheck-caption {
  color: #3d4e67;
}

/*----Image Check----*/

