/* ---Video ----*/

#index-video {
  .index-video-container {
    position: relative;
    overflow: hidden;

    &:after {
      bottom: 0;
      content: "";
      left: 0;
      opacity: .7;
      position: absolute;
      right: 0;
      top: 0;
      z-index: 1;
    }

    .videocaption {
      left: 1%;
      position: absolute;
      right: 1%;
      text-align: center;
      top: 30%;
      z-index: 2;

      h2 {
        color: $white;
        font-size: 4.5em;
        font-weight: 900;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;
        border: none;
        color: rgba(255, 255, 255, 1);
        text-align: center;
        -o-text-overflow: clip;
        text-overflow: clip;
        text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.0980392), 0 0 5px rgba(0, 0, 0, 0.0980392), 0 1px 3px rgba(0, 0, 0, 0.298039), 0 3px 5px $black-2, 0 5px 10px rgba(0, 0, 0, 0.247059), 0 10px 10px $black-2, 0 20px 20px rgba(0, 0, 0, 0.14902);
        -webkit-transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
        -moz-transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
        -o-transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
        transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
      }

      h4 {
        color: $white;
        font-size: 2em;
        margin-top: 25px;
      }

      .btn-slide {
        background: #f62459;
        color: $white;
        font-size: 16px;
        font-weight: 300;
        margin: 25px auto 10px auto;
        padding: 20px 50px;
        border-radius: 0;
        text-transform: uppercase;

        &:hover {
          background: $white;
          color: #333;
        }
      }
    }
  }

  video {
    width: 100%;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
  }
}

